    font-family: var(--font-family-inter-regular);
    font-size: 16px;
    padding: 10px;
    border-radius: 12px;
    min-width: 150px;
    text-transform: capitalize;
    margin-bottom: 30px;
    margin-left: 5px;
  }
}
